 20px 0 20px 0;\n}\n.header-dashboard{\n    width: 100%;\n    background: #ccc;\n    float: left;\n    text-align: center;\n    font-weight: 500;\n    font-size:20px;\n    min-height: 50px;\n    overflow-y: hidden;\n    padding-top: 10px;\n    float: left;\n}\n.available-lesson{\n    text-align: left;\n    padding-left: 20px;\n    margin-top: 10px;\n}\n.wistia-holder{\n    float: left; \n    height: 300px;\n    width: 100%;\n}\n.redirect-buttons{\n    height: 50px;\n    width: 20%;\n    float: left;\n    margin-right: 1%;\n    margin-top: 10px;\n    font-size:20px;\n    text-align: center;\n}\n.redirect-buttons label{\n    padding-top: 10px;\n}\n.redirect-buttons:hover{\n    cursor: pointer;\n}\n.speech-bubble {\n    position: relative;\n    background: #1caceb;\n    border-radius: 4em;\n    text-align: center;\n    height: 450px;\n    padding-top: 10%;\n}\n.speech-bubble:after {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  left: 50%;\n  width: 0;\n  height: 0;\n  border: 20px solid transparent;\n  border-top-color: #1caceb;\n  border-bottom: 0;\n  border-left: 0;\n  margin-left: -10px;\n  margin-bottom: -20px;\n}\n.speech-bubble h1{\n  color: white;\n  font-size: 150px;\n}\n.speech-bubble h2{\n  color: gray;\n  font-size: 75px;\n}\n.table{\n  margin-top: 50px;\n  margin-bottom: 50px;\n}\n.top-lesson-holder{\n  width: 100%;\n  min-height: 40px;\n  float: left;\n  overflow: hidden;\n}\n.top-lesson-content{\n  width: 100%;\n  float: left;\n  height: 100%;\n  margin-bottom: 10px;\n}\n.category-lesson-holder{\n  width: 90%;\n  min-height: 40px;\n  float: left;\n  margin-left: 10%;\n  overflow: hidden;\n  margin-bottom: 10px;\n}\n.category-lesson-content{\n  width: 100%;\n  float: left;\n  height: 100%;\n}\n.lesson-holder{\n  width: 90%;\n  margin-left: 10%;\n  float: left;\n  padding-top: 15px;\n  padding-bottom: 10px;\n  font-size: 16px;\n  border-bottom: 1px solid #ccc;\n}\n.lesson-holder-index{\n  padding-left: 10px;\n  padding-right: 10px;\n}\n.lesson-holder:hover{\n  background: #1caceb;\n  cursor: pointer;\n  color: white;\n}\n.lesson-holder:hover .test-check{\n  color: #1caceb;\n  background: #fff;\n}\n.lesson-holder-text{\n  float: left;\n  height: 100%;\n  margin-right: 20px;\n}\n.lesson-holder-test-checker{\n  float: left;\n  height: 100%;\n}\n.test-check-number, .test-check{\n  background: #888;\n  padding-top: 7px;\n  width: 40px;\n  height: 40px;\n  border-radius: 20px;\n  color: #fff;\n  text-align: center;\n}\n.test-check{\n  color: #fff;\n  text-align: center;\n  background: #1caceb;\n}\n.test-check i{\n  font-size: 18px;\n}\n.modal-body{\n  min-height: 50px !important;\n  overflow: hidden;\n}\n/*\n  CONTENT\n*/\n.mobile-viewer{\n  display: none;\n}\n.mobile-viewer-button1, .mobile-viewer-button2{\n  width: 48%;\n  float: left;\n  text-align: center;\n  border: 1px solid #ccc;\n  margin-bottom: 20px;\n  margin-top: 20px;\n  height: 50px;\n}\n.mobile-viewer-button1{\n  margin-left: 2%;\n}\n.mobile-viewer-button1 h5, .mobile-viewer-button2 h5{\n  line-height: 50px;\n  margin-bottom: 0px;\n}\n.mobile-viewer-button1:hover, .mobile-viewer-button2:hover{\n  cursor: pointer;\n  background: #1caceb;\n  color: #fff;\n  border: 1px solid #1caceb;\n}\n.content-manager{\n  float: left;\n  width: 55%;\n  display: block;\n}\n.lesson-content-holder{\n  width: 96%;\n  min-height: 200px;\n  overflow: hidden;\n  margin: 0 2% 0 2%;\n  float: left;\n}\n.content-translation{\n  font-size: 20px;\n}\n.content-translation{\n  color: #ccc;\n}\n.content-items{\n  float: left;\n  width: 90%;\n  margin-left: 10%;\n}\n.content-items label{\n  margin-bottom: 0px;\n}\n/*\n      \n    GRAMMAR AND CULTURE TIP\n*/\n.lesson-content-tip{\n  width: 100%;\n  min-height: 200px;\n  float: left;\n  font-size: 16px !important;\n  overflow: hidden;\n  margin-top: 50px;\n}\n.tip-header{\n  width: 96%;\n  float: left;\n  margin: 0 2% 0 2%;\n}\n.fa-play-circle{\n  color: #1caceb;\n  padding-right: 10px;\n  font-size: 50px;\n}\n.tip-header .icon{\n  width: 8%;\n  float: left;\n}\n.tip-header .text{\n  width: 92%;\n  float: left;\n  padding-top: 10px;\n}\n.tip-content{\n  width: 96%;\n  float: left;\n  margin: 0 2% 0 2%;\n}\n.tip-content label{\n  text-align: justify;\n}\n/*\n    \n  AUDIO MANAGER\n*/\n.audio-manager{\n  float: left;\n  width: 45%;\n  display: none;\n  background: #eee;\n  min-height: 450px;\n  overflow: hidden;\n}\n.close-audio-manager{\n}\n.is-word-audio, .is-sentence-audio{\n  display: block;\n}\n.audio-holder{\n  width: 96%;\n  float: left;\n  margin: 20px 2% 0 2%;\n}\n.audio-icons{\n  float: left;\n  min-height: 50px;\n  width: 20%;\n  overflow-y: hidden;\n}\n#audio-content{\n  min-height: 50%;\n  width: 80%;\n  float: left;\n  overflow-y: hidden;\n}\n#audio-content #word, #audio-content #translation{\n  min-height: 25px;\n  width: 100%;\n  float: left;\n  overflow-y: hidden;\n}\n#audio-content #word{\n  font-size: 20px;\n  font-weight: 400;\n}\n#audio-content #word b, #audio-content #translation b{\n  font-weight: 600;\n}\n#audio-content #translation{\n  font-weight: 400;\n  font-size: 18px;\n  color: #555;\n}\n.small-audio-icons{\n  height: 40px;\n}\n.small-word{\n  height: 20px;\n  float: left;\n  font-weight: 600;\n}\n.small-audio:hover{\n  text-decoration: underline;\n  cursor: pointer;\n}\n.small-translation{\n  height: 20px;\n  width: 85%;\n  float: left;\n  margin-left: 5%;\n  color: #555;\n}\n.lesson-button-active{\n  background: #fff;\n  border: solid 2px #1caceb;\n  color: #1caceb;\n}\n.audio-button-active, .grammar-button-active, .culture-button-active, .visualizer-button-active, .syllabus-button-active{\n  background: #1caceb;\n  border: solid 2px #fff;\n  color: #fff;\n}\n.audio-close{\n  padding: 10px;\n  color: #fff;\n}\n.audio-close:hover{\n  cursor: pointer;\n}\n.audio-content-holder{\n  min-height: 60px;\n  width: 100%;\n  float: left;\n  overflow-y: hidden;\n}\n/*\n      STRESS AND ACCENT\n*/\n.accent-holder{\n  width: 96%;\n  float: left;\n  height: auto;\n  margin: 0 2% 0 2%;\n  background: #fff;\n  text-align: justify;\n}\n.accent-header-holder{\n  width: 100%;\n  float: left;\n  min-height: 50px;\n  overflow-y: hidden;\n  color: #1caceb;\n}\n.accent-header{\n  width: 98%;\n  float: left;\n  min-height: 30px;\n  font-weight: 600;\n  margin: 0 1% 0 1%;\n  z-index: 10000;\n  font-size: 28px !important;\n  overflow-y: hidden;\n  text-align: justify;\n  display: inline;\n}\n.accent-header label{\n  display: initial;\n  text-transform: capitalize;\n}\n.accent-header span{\n  text-transform: none;\n}\n.accent-header span span{\n  display: inline-block;\n}\n.accent-header a:hover{\n  cursor: pointer !important;\n  text-decoration: underline !important;\n}\n.accent-item-holder{\n  height: 300px;\n  width: 100%;\n  overflow-y: scroll;\n  float: left;\n}\n.accent-item:last-child{\n  margin-bottom: 100px !important;\n}\n.accent-header-extra{\n  width: 98%;\n  float: left;\n  height: 30px;\n  font-weight: 600;\n  margin: 0 1% 0 1%;\n  font-size: 20px !important;\n}\n.accent-header-extra-info{\n  width: 98%;\n  float: left;\n  position: relative;\n  font-weight: 600;\n  margin: 0 1% 0 1%;\n  font-size: 20px !important;\n}\n.accent-item{\n  height: auto;\n  width: 96%;\n  margin: 10px 2% 10px 2%;\n  float: left;\n}\n.accent-content{\n  width: 100%;\n  float: left;\n  height: auto;\n}\n.accent-content-left{\n  width: 22%;\n  float: left;\n}\n.accent-audio{\n  width: 50%;\n  float: left;\n}\n.accent-letter{\n  width: 50%;\n  float: left;\n  margin-top: -15px;\n  text-align: center;\n}\n.accent-letter label{\n  width: 100%;\n  float: left;\n  font-size: 50px;\n}\n.accent-letter .bottom{\n  font-size: 15px;\n  margin-top: -20px;\n  color: #555;\n}\n.accent-letter-defi{\n  width: 72%;\n  margin-left: 6%;\n  float: left;\n  text-align: justify;\n}\n.accent-letter-defi ul{\n  float: left;\n}\n.accent-letter-defi-extra{\n  width: 88%;\n  float: left;\n  text-align: justify;\n}\n.accent-media-holder{\n  width: 100%;\n  float: left;\n  text-align: center;\n}\n.accent-media-holder-extra{\n  width: 100%;\n  float: left;\n  text-align: center;\n}\n.accent-media-item{\n  float: left;\n  height: auto;\n  width: 100%;\n  margin-top: 5px;\n}\n.accent-media-item:hover{\n  cursor: pointer;\n  border: 1px solid #ccc;\n}\n.audio-list-holder{\n  height: auto;\n  width: 96%;\n  margin: 25px 2% 25px 2%;\n  float: left;\n  font-size: 16px !important;\n}\n.audio-list-holder .word-images-holder{\n  width: 100%;\n  float: left;\n  min-height: 102px;\n  margin-bottom: 25px;\n  overflow-y: auto;\n}\n.word-images-holder .src-image-holder{\n  float: left;\n  width: 76px;\n  height: 102px;\n  margin-right: 2px;\n}\n.src-image-holder img{\n  max-width: 100%;\n  height: 102px;\n}\n.word-images-holder .src-image-holder:hover{\n  cursor: pointer;\n}\n.audio-list-item{\n  height: auto;\n  width: 100%;\n  float: left;\n}\n.audio-list-item .original{\n  padding-left: 10px;\n}\n.audio-list-item .translation{\n  padding-left: 20px;\n}\n/*\n  AUDIO VISUALIZER\n    \n*/\n.audio-visualizer{\n  max-height: 64px;\n  width: 100%;\n  float: left;\n  overflow: hidden;\n  margin-bottom: 20px;\n  margin-top: 20px;\n  visibility: hidden;\n}\n.audio-visualizer > canvas{ \n  width:90%;\n  height:70px;\n  margin: 15px 5% 15px 5%;\n  float:left;\n}\n.spinner{\n  width: 100%;\n  height: 60px;\n  font-size: 50px;\n  line-height: 60px;\n  z-index: 10;\n  text-align: center;\n  display: none;\n}\n.fa-spin {\n  animation-duration: 1s;\n}\n.progress{\n  width: 100%;\n  height: 20px;\n  float: left;\n}\n.active-visualizer{\n  visibility: visible;\n}\n.hide-progress-bar{\n  display: none;\n}\n/* Tooltip container */\n.tooltips {\n  position: relative;\n  display: inline-block;\n  color: gray;\n  font-size: 20px;\n  z-index: 10000 !important;\n}\n.tooltipsAudio{\n  position: relative;\n  display: inline-block;\n  color: gray;\n  color:#000;\n  margin-right: 5px;\n  font-size: 16px;\n}\n.tooltipsAudio label{\n  margin-bottom: 0px;\n}\n.number{\n  color: gray;\n  font-size: 20px;\n}\n.number:hover{\n  cursor: pointer;\n  text-decoration: underline;\n  color: #1caceb;\n}\n.number-clicked{\n  color: black;\n}\n.tooltips-new-line {\n    position: relative;\n    color: gray;\n}\n.tooltips-clicked{\n    color: #000 !important;\n}\n/* \n    Tooltip text \n*/\n.tooltips .tooltipTitle,  .tooltips-new-line .tooltipTitle{\n    visibility: hidden;\n    width: 120px;\n    background-color: #e1e1e1;\n    color: black;\n    text-align: center;\n    border-radius: 6px;\n    padding: 5px 0;\n    position: absolute;\n    z-index: 1;\n    bottom: 125%;\n    left: 50%;\n    margin-left: -60px;\n    opacity: 0;\n    transition: opacity 1;\n    white-space: pre-line;\n    font-size: 20px;\n}\n.tooltipsAudio .tooltipTitleAudio{\n    visibility: hidden;\n    width: 80px;\n    background-color: #e1e1e1;\n    color: black;\n    text-align: center;\n    border-radius: 6px;\n    padding: 5px 0;\n    position: absolute;\n    z-index: 1;\n    bottom: 125%;\n    left: 50%;\n    margin-left: -40px;\n    opacity: 0;\n    transition: opacity 1;\n    white-space: pre-line;\n    font-size: 12px;\n}\n/* Tooltip arrow */\n.tooltips .tooltipTitle::after, .tooltips-new-line .tooltipTitle::after{\n    content: \"\";\n    position: absolute;\n    top: 100%;\n    left: 50%;\n    margin-left: -5px;\n    border-width: 5px;\n    border-style: solid;\n    border-color: #e1e1e1 transparent transparent transparent;\n}\n.tooltipsAudio .tooltipTitleAudio::after{\n    content: \"\";\n    position: absolute;\n    top: 100%;\n    left: 50%;\n    margin-left: -5px;\n    border-width: 5px;\n    border-style: solid;\n    border-color: #e1e1e1 transparent transparent transparent;\n}\n.audio-list-button{\n  width: 20%;\n  float: left;\n  margin-bottom: 5px;\n}\n.extension-holder{\n  width: 80%;\n  float: left;\n  margin-bottom: 5px;\n}\n.audio-list-content{\n  width: 100%;\n  float: left;\n}\n.audio-list-translation{\n  width: 100%;\n  float: left;\n  color: #aaa;\n}\n/* Show the tooltip text when you mouse over the tooltip container */\n.tooltips:hover .tooltipTitle, .tooltips-new-line:hover .tooltipTitle, .tooltipsAudio:hover .tooltipTitleAudio {\n    visibility: visible;\n    opacity: 1;\n}\n/*\n    GUIDE VIDEO\n*/\n.guide-holder{\n  width: 70px;\n  margin-right: 10px;\n  right: 0;\n}\n.guide-holder label{\n  float: left;\n  text-transform: uppercase;\n  font-size: 10px;\n  font-weight: 600;\n}\n.guide-holder .guide-video{\n  float: left;\n  height: 40px;\n  width: 70px;\n  margin-top: 5px;\n}\n.account-error .error-message{\n  width: 100%;\n  float: left;\n  min-height: 10px;\n  overflow-y: hidden;\n  margin-top: 10px;\n}\n.account-error .error-action{\n  width: 100%;\n  float: left;\n  min-height: 10px;\n  overflow-y: hidden;\n  margin-top: 10px;\n}\n.show-on-desktop{\n  display: block;\n}\n.show-on-mobile{\n  display: none;\n}\n.margin-audio-mobile{\n  margin-top: 0px;\n}\n.popover_content, .pronounce_popover_content {\n  transform: translate(0,10px);\n  padding: 10px;\n  width: auto;\n  background: #1caceb;\n  border: solid 1px #1caceb;\n  min-height: 10px;\n  font-size: 15px;\n  color: #fff;\n  font-weight: 400;\n  text-align: justify;\n  display: none;\n  border-bottom-right-radius: 2px;\n  border-bottom-left-radius: 2px;\n  border-top-right-radius: 2px;\n  position: absolute;\n  z-index: 10000;\n}\n.popover_content .header-popover, .pronounce_popover_content .header-popover{\n  float: left;\n  width: 100%;\n  height: 30px;\n  text-align: right;\n}\n.header-popover i{\n  line-height: 30px;\n  padding-bottom: 5px;\n  font-size: 15px;\n}\n.popover_content:before {\n  position: absolute;\n  z-index: -1;\n  content: '';\n  left: 5%;\n  top: -10px;\n  border-style: solid;\n  border-width: 0 10px 10px 10px;\n  border-color: transparent transparent #1caceb transparent;\n  transition-duration: 0.3s;\n  transition-property: transform;\n}\n.pronounce_popover_content:before {\n  position: absolute;\n  z-index: -1;\n  content: '';\n  left: 0%;\n  top: